import React from 'react';
import axios from 'axios';
import Movie from './Movie';
import "./App.css";

/*  üôÜ‚Äç‚ôÄÔ∏è Proper Type Check üôÜ‚Äç‚ôÄÔ∏è */
// Check info ‚û°Ô∏è https://reactjs.org/docs/typechecking-with-proptypes.html

/* üèÉ‚Äç‚ôÇÔ∏è Run!  */
class App extends React.Component {
  
  state = { /* ÏïûÏúºÎ°ú ÏÇ¨Ïö©Ìï† Î≥ÄÏàòÏóê ÎåÄÌï¥ stateÏóêÏÑú Î®ºÏ†Ä Ï†ïÏùòÌïòÎäî ÏäµÍ¥Ä */
    isLoading: true,
    movies: []
  };

  /* üóÑ Data  */
  getMovies = async () => {  // async: ÎπÑÎèôÍ∏∞Ìï®Ïàò. ÎÑå Í∏∞Îã§Î†§ÏïºÌï¥
    const { 
      data: { 
        data: { movies }
      }
    } = await axios.get(
      "https://yts-proxy.now.sh/list_movies.json?sort_by=rating"
    ); // await: awaitÏóê Í±∏Î†§ÏûàÎäî Ìï®Ïàò ÏãúÌñâÏùÑ Í∏∞Îã§Î†§Ïïº Ìï¥
    this.setState({ movies: movies, isLoading: false })
  };
  
  componentDidMount(){
    this.getMovies();
  }
  
  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        { isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
          ) : (
            <div className="movies">
              {movies.map(movie => (
                <Movie
                  key      = {movie.id}
                  id       = {movie.id}
                  title    = {movie.title}
                  year     = {movie.year}
                  summary  = {movie.summary}
                  poster   = {movie.medium_cover_image}
                  trailer  = {movie.yt_trailer_code}
                  genres   = {movie.genres}
                />
              ))}
            </div>
            )}
      </section>
    );
  }
}

export default App;
